@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: hsl(212, 45%, 89%);
  font-size: 15px;
  font-family: "Outfit", sans-serif;
  width: 100%;
  height: 100vh;
  place-content: center;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 1em;
  max-height: calc(100vh - 40%);
  min-height: fit-content;
  width: 90%;
  max-width: 300px;
  margin: 0 auto;
  background: hsl(0, 0%, 100%);
  border-radius: 1em;
}
.container figure {
  width: 100%;
  height: 60%;
}
.container figure img {
  width: 100%;
  height: 100%;
  border-radius: 1em;
  object-fit: cover;
}
.container .text-container {
  text-align: center;
  width: 95%;
  height: 40%;
  min-height: fit-content;
  padding: 0.4em;
}
.container .text-container h1 {
  font-size: 1.5em;
  font-weight: 700;
  color: hsl(218, 44%, 22%);
  margin-bottom: 20px;
}
.container .text-container p {
  line-height: 1.6;
  color: hsl(216, 15%, 48%);
  font-weight: 500;
}